.app{
    height: 100vh;
    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        // background-color: black;
        position: relative;
        top: 70px; 
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        // scrollbar-width: 10px; // firefox
        // scrollbar-color: black;
        &::-webkit-scrollbar{
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
} 